.nav-wrap {
  position: sticky;
  display: flex;
  align-items: center;
  z-index: 999;
  color: #fff;
  border-radius: 4px;
  top: 1.4em;
  background: rgba(0, 0, 0, 0.1);
  -webkit-backdrop-filter: blur(8px);
          backdrop-filter: blur(8px);
  width: calc(100% - 195px);
  margin: 20px auto;
  height: 70px;
  transition: box-shadow 0.2s ease-in-out;
  box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.04);
}
.nav-wrap::before {
  content: "";
