  border-radius: 50%;
  position: absolute;
  cursor: pointer;
  z-index: 100;
  transition: all cubic-bezier(0.8, 0, 0.2, 1) 0.4s, opacity 0s;
}
.post-content .after-container .after-btn:hover {
  transform: scale(0.6);
}
.post-content .after-container .after-btn:hover::after {
  opacity: 0;
}
.post-content .after-container .after-btn::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #fff;
  z-index: 2;
  transition: opacity 0.6s;
  animation: pulse 1.5s cubic-bezier(0.8, 0, 0.2, 1) 0s infinite;
}
@keyframes pulse {
  0% {
    transform: scale(0.4);
  }
  33% {
    transform: scale(1);
  }
  66% {
    transform: scale(0.4);
  }
  100% {
