.button {
  opacity: 1;
  font-weight: 400;
  display: inline-flex;
  justify-content: center;
  border-radius: 10px;
  padding: 10px 12px;
  transition: all 0.6s ease, border 0.1s ease-in;
  background: #151618;
  border: 1px solid rgba(255, 255, 255, 0.1);
  cursor: pointer;
  &.active {
    background: rgba(255, 255, 255, 0.05);
  }
  p {
    color: $color-white;
    white-space: nowrap;
    pointer-events: none;
  }
  &:hover {
    background: rgba(255, 255, 255, 0.05);
  }
}
.post-content {
  display: flex;
  gap: 20px;
  .button {
    color: #151719;
    background: rgba(21, 22, 24, 0.1);
    border: 1px solid #c4c4c4;
  }
}
